import React, { useState } from 'react';
import './../styles/AddAnime.css';

function AddAnime({data, dataChange}) {

    // change error if the value of input is 0

    const [handleP, setHandleP] = useState(false);

    // add new anime

    const handleSubmit = (e) => {
        e.preventDefault();
        let character = e.target[0].value;
        let anime = e.target[1].value;

       let newId;
       const dataId = data.find(anime=> anime.id === data.length + 1);

       dataId ? newId = data.length + Math.floor(Math.random()*(20-3)+3) : newId = data.length + Math.floor(Math.random()*(40-2)+2);

       if(character.length > 0 && anime.length > 0){
           dataChange([...data,{
               id: newId, anime: anime, character: character
           }]);
           setHandleP(false);
       } else {
            setHandleP(true)
       }


        e.target[0].value = "";
        e.target[1].value = "";
    }

    return (
        <form onSubmit={handleSubmit} action="">
            <div className="container-input">
                <input name="character" type="text" placeholder="Personaje"/>
                <input name="anime" type="text" placeholder="Anime"/>
                { handleP ? <p>COMPLETA TODOS LOS DATOS</p> : ""}
            </div>
            <div className="container-btn">
                <button type="submit">Agregar</button>
            </div>
        </form>
    )
}

export default AddAnime
