import React from 'react';
import './../styles/EditAnime.css';

function EditAnime({setModal, valueInputModal, setValueInpuModal, idForChangeInput, characters, setCharacters}) {

    // cancel edit

    const cancelEdit = () => {
        setModal(false);
    }

    // edit new value
    const edit = () => {

        setCharacters(characters.map(an => an.id === idForChangeInput ? valueInputModal : an ))
       setModal(false)
    }

    // change the value of inputs

    const changeValueInputAnime = (e) => {
        setValueInpuModal({
            id: valueInputModal.id,
            character: valueInputModal.character,
            anime: e.target.value
        })
    }

    const changeValueInputCharacter = (e) => {
        setValueInpuModal({
            id: valueInputModal.id,
            character: e.target.value,
            anime: valueInputModal.anime
        })
    }

    // prevent default of form

    const preventDefualt = (e) => {   
        e.preventDefault()
    }

    return (
        <form onSubmit={preventDefualt} className="form-modal" action="">
            <div className="container-modal">
                <div className="container-modal__inputs">
                    <input type="text" placeholder="Personaje" onChange={changeValueInputCharacter} defaultValue={valueInputModal.character}/>
                    <input type="text" placeholder="Anime" onChange={changeValueInputAnime} defaultValue={valueInputModal.anime}/>
                </div>
                <div className="container-modal__btns">
                    <button onClick ={edit}>Editar</button>
                    <button onClick={cancelEdit}>Cancelar</button>
                </div>
            </div>
        </form>
    )
}

export default EditAnime


