import { Fragment, useState } from 'react';
import AddAnime from './Components/AddAnime';
import EditAnime from './Components/EditAnime';
import Home from './Components/Home';
import './App.css';

// data of table
const data = [
  {id: 1, anime: 'Naruto', character: 'Itachi'},
  {id: 2, anime: 'Shingeky No Kyojin', character: 'Levi'},
  {id: 3, anime: 'One Piece', character: 'Trafalgar Law'},
  {id: 4, anime: 'Bleach', character: 'Ulquiorra'},
  {id: 5, anime: 'Boku No Hero', character: 'Sota'},
  {id: 6, anime: 'Dragon Ball Z', character: 'Vegeta'},
]

function App() {

  // useState of animes
  const [characters, setCharacters] = useState(data);

  // 

  const [valueInputModal, setValueInpuModal] = useState({
    id: null,
    character: "",
    anime: ""
  });

 

  // 

  const [modal, setModal] = useState(false);
  
  // 

  const [idForChangeInput, setIdForChangeInput] = useState(0) 
  
  const findButtonId = e => {
    characters.map((characters)=> characters.id == e.target.id ? setValueInpuModal({character: characters.character, anime: characters.anime, id: characters.id}):"");

    characters.map((character)=> character.id == e.target.id ? setIdForChangeInput(character.id) :"")

    setModal(true)
  }

  // delete tr of table

  const delet = (id) => {
    setCharacters(characters.filter(anime => anime.id !== id))
  }

  return (
    <Fragment>
        <AddAnime dataChange={setCharacters} data={characters}/>
        <Home data={characters} setModal={setModal} findButtonId ={findButtonId} delet = {delet}/>
        {/* modal */}
        {modal ? <EditAnime characters={characters} setCharacters={setCharacters} idForChangeInput={idForChangeInput} setModal={setModal} valueInputModal={valueInputModal} setValueInpuModal={setValueInpuModal}/> : ""}
       
    </Fragment>
  );
}

export default App;
