import React from 'react';
import './../styles/Home.css';

function Home({data, findButtonId, delet}) {

    

    return (
        <table>
            <thead>
                <tr>
                    <th>id</th>
                    <th>Personaje</th>
                    <th>Anime</th>
                    <th>Acciones</th>
                </tr>
            </thead>
            <tbody>
                {
                    data.map(anime => (
                        
                        <tr key={anime.id}>
                            <td>{anime.id}</td>
                            <td>{anime.character}</td>
                            <td>{anime.anime}</td>
                            <td><button onClick={findButtonId} id={anime.id}>Editar</button><button onClick={()=>{delet(anime.id)}}>Elminar</button></td>
                        </tr>
                    ))
                }
            </tbody>
        </table>
    )
}

export default Home
